@import "../../variables.scss";

.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 240px;

  margin: 0 auto;

  &__actions {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: space-between;

    color: $fontColorLight50;

    font-size: 12px;
  }

  &__actions-start-item,
  &__actions-end-item {
    border-radius: 20px;

    background: $secondColorGradient;
  }

  &__actions-start-item svg path,
  &__actions-end-item svg path {
    fill: white;
  }

  &__actions-start-item svg {
    transform: rotateY(180deg);
  }

  li.pagination__actions-filler {
    cursor: auto;
  }

  &__actions-item_selected {
    border-radius: 20px;

    color: white;
    font-weight: bold;
    background: $mainColorGradient;
  }

  &__description {
    display: block;
    width: 100%;

    margin-top: 10px;

    color: $fontColorLight75;

    font-size: 14px;
    text-align: center;
  }

  &_start &__actions-start-item {
    display: none;
  }

  &_end &__actions-end-item {
    display: none;
  }
}

[class^="pagination__actions-"] {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;

  cursor: pointer;
}
