@import "../../variables.scss";

.room-item {
  display: block;
  width: 100%;
  min-width: 270px;

  color: $fontColorLight75;

  box-shadow: 0 10px 20px $fontColorLight5;
  border-radius: 4px;

  &__info {
    padding: 20px;
  }

  &__info-top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-bottom: 1px solid $fontColorLight5;
    padding-bottom: 10px;
    padding-top: 4px;
  }

  &__info-number span {
    margin-right: 7px;

    font-family: $headerFont;
    font-size: 19px;
    line-height: 14px;
    font-weight: bold;
  }

  &__info-number span:first-child {
    font-size: 14px;
  }

  &__info-number &__info-lux {
    color: $mainColor;

    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
  }

  &__info-cost {
    margin-right: -4px;

    color: $fontColorLight50;

    font-size: 14px;
    font-weight: bold;
  }

  &__info-cost span:nth-child(1) {
    margin-right: 0;
  }

  &__info-cost span:last-child {
    font-weight: normal;
    font-size: 12px;
    margin-left: 4px;
  }

  &__info-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 10px;
  }

  &__info-rate {
    margin-top: -2px;
  }

  &__feedback-count {
    padding-top: 2px;

    color: $fontColorLight50;

    font-size: 14px;
    font-weight: bold;
  }

  &__feedback-count span:last-child {
    font-weight: normal;
    margin-left: 4px;
  }
}
